* {
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

/* Nav Bar */
nav {
    background-color:#9FD971;
    height: 120px;
}

.chef-hat {
    width: 145px;
    margin: 0px;
}

/* Home */
.color-overlay {
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom right, black, gray);
    position: absolute;
    opacity: .2;
    top: 0px;
} 

.home-text {
    display: flex;
    justify-content: center;
    color: white;
    position: relative;
    /* width: 500px; */
    text-shadow: 4px 4px 5px black;
}

.home-text h1 {
    text-align: center;
    width: 625px;
    padding-top: 70px;
    font-size: 2rem;
}

.button-container {
    display: flex;
    justify-content: center;
    font-size: 2.3rem;
}

.home-button {
    width: 180px;
    height: 63px;
    font-size: 2rem;
    margin-top: 90px;
    background-color: #9FD971;
    border-radius: 40px;
    text-shadow: 1px 1px 5px black;
    box-shadow: inset 2px 2px 5px black;
    box-shadow: 2px 2px black;
}


/* Footer */
footer {
    display: flex;
    justify-content: center;
    background-color: #efefef;
    
}


/* Responsive */
@media screen and (max-width: 960px) {

    /* Navbar */
    .nav {
        background-color:#9FD971;
        height: 120px;
    }

    .chef-hat {
        display: none;
        width: 145px;
        margin: 0px;
    }

    /* Home */
    .home-text {
        display: flex;
        justify-content: center;
        color: white;
        font-size: 1rem;
        position: relative;
        /* width: 500px; */
        text-shadow: 4px 4px 5px black;
        padding: 10px;
    }

    .home-text h1 {
        text-align: center;
        width: 625px;
        padding-top: 70px;
        font-size: 2rem;
    }

    /* Footer */
    footer img {
        width: 100px;
    }
}